import { Routes } from '@angular/router';
import { ErrorComponent } from './error/error.component';


export const ROUTES: Routes = [{
   path: '', redirectTo: 'login', pathMatch: 'full'
  }, {
    path: 'app',   loadChildren: () => System.import('./layout/layout.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod)
  }, {
    path: 'login', loadChildren: () => System.import('./login/login.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod)
  }, {
    path: 'error', component: ErrorComponent
  }, {
    path: '**',    component: ErrorComponent
  }
];



